<template>
	<div id="shop-cart" class="font-14">
		<public-header title="购物车"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<van-checkbox-group v-model="result" class="bg-default" v-if="icart.num > 0">
				<template v-for="(item, itemIndex) in icart.data">
					<van-checkbox
						v-for="(option, index) in item"
						:key="option.id"
						:name="item"
						checked-color="#ff2d4b"
						:class="{'border-1px-t': itemIndex > 0}"
						v-if="option.goods_id != '88888'"
					>
						<div class="goods-item">
							<div class="goods-thumb">
								<img :src="option.thumb" alt="" class="img-100">
							</div>
							<div class="info">
								<div class="goods-title">
									{{option.title}}
								</div>
								<div class="goods-price">
									{{Lang.dollarSign}}{{option.total_discount_price}}
								</div>
							</div>
							<goods-handle
								:goods="option"
								from="cart"
								:optionId="index"
								:showAnimate="false"
							>
							</goods-handle>
						</div>
					</van-checkbox>
				</template>
			</van-checkbox-group>
			<div class='no-data'v-else>
				<img class='no-cart' src='static/img/cart_con.png'>
				<span class='no-record'>您还没有添加购物车，快去购买吧</span>
				<router-link tag="div" :to="util.getUrl({path: '/pages/shop/index', query: {sid: sid}})" class="target">现在去购物</router-link>
			</div>
		</div>
		<div class="coupons-wrap van-hairline--bottom">
			<div class="title">领券</div>
			<div class="coupon-inner" v-if="coupons.id > 0">
				<div class="coupons">
					<div class="coupon-item" v-for="(item, index) in coupons.coupons" :key="index">满{{item.condition}}减{{item.discount}}</div>
				</div>
				<i class="icon icon-right font-12 c-gray"></i>
			</div>
			<div class="coupon-inner" v-else>暂无领券活动~</div>
		</div>
		<div class="total-wrap van-hairline--top van-hairline--bottom" v-if="icart.num > 0">
			<van-checkbox v-model="checkAll" checked-color="#ff2d4b" v-if="0">全选</van-checkbox>
			<div class="total">
				<div class="info">
					<div>
						<span>合计</span>
						<span class="c-danger">{{Lang.dollarSign}}</span>
						<span class="font-bold c-danger font-16">{{cartTotalPrice}}</span>
					</div>
					<div class="discount" v-if="icart.box_price > 0">
						其中含餐盒费{{Lang.dollarSign}}{{icart.box_price}}
					</div>
				</div>
				<van-button round size="normal" type="danger" disabled v-if="cartSendCondition > 0">还差{{Lang.dollarSign}}{{cartSendCondition}}</van-button>
				<van-button round size="normal" type="danger" :to="util.getUrl({path: '/pages/order/create', query: {sid: istore.id}})" v-else>去结算({{icart.num}})</van-button>
			</div>
		</div>
		<div class="delete" v-if="0">删除</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import GoodsHandle from '@/components/goodsHandle'
	import {mapState, mapMutations} from 'vuex'
	export default {
		data() {
			return {
				sid: 0,
				coupons: {
					coupons: []
				},
				showPreLoading: true,
				menufooter: {},
				checkAll: false,
				result: [],
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'wmall/shop/cart',
					data: {
						sid: this.sid,
						menufooter: 1
					},
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					result = result.message;
					this.coupons = this.util.extend(this.coupons, result.coupons)
					this.replaceStore(result.store);
					this.replaceCart(result.cart.message.cart);

					this.menufooter = window.menufooter;
				})
			},

			onToggleGoods() {

			},

			...mapMutations([
				'replaceStore', 'replaceCart'
			]),
		},

		created() {
			this.query = this.util.parseQuery(this.$route.query);
			this.sid = this.query.sid;
		},
		mounted() {
			var that = this;
			that.onLoad();
		},

		components: {
			PublicHeader,
			PublicFooter,
			GoodsHandle
		},
		computed: {
			...mapState([
				'istore', 'icart',
			]),

			cartTotalPrice: function (){
				return (this.icart.price + this.icart.box_price).toFixed(2);
			},

			cartSendCondition: function (){
				if(!this.from) {
					if(!this.icart.num) {
						return this.istore.send_price;
					} else {
						return (this.istore.send_price - this.icart.price - this.icart.box_price).toFixed(2);
					}
				} else if(this.from == 'reserve') {
					if(!this.icart.num) {
						return this.table_category.limit_price;
					} else {
						return (this.table_category.limit_price - this.icart.price).toFixed(2);
					}
				} else if(this.from == 'table') {
					if(this.icart.num) {
						return 0;
					}
				}

			}
		},
	}
</script>

<style>
	@import "../../../static/css/test.css";
	#shop-cart .content{
		padding: 58px 0 51px 0;
	}
	#shop-cart .content .van-checkbox{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		padding: 15px;
	}
	#shop-cart .van-checkbox__icon{
		display: none;
	}
	#shop-cart .content .van-checkbox__label{
		width: 100%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		margin-left: 0;
	}
	#shop-cart .coupons-wrap{
		position: fixed;
		top: 46px;
		left: 0;
		right: 0;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		padding: 15px 10px;
		background-color: #fff;
	}
	#shop-cart .coupons-wrap .coupon-inner{
		width: 88%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#shop-cart .coupons-wrap .coupons{
		width: 92%;
		overflow-x: scroll;
		overflow-y: hidden;
		white-space: nowrap;
	}
	#shop-cart .coupons-wrap .coupons .coupon-item{
		display: inline-flex;
		padding: 3px 5px;
		margin-right: 5px;
		font-size: 12px;
		color: #fff;
		background-color: rgba(255, 45, 75, 0.4);
	}
	#shop-cart .coupons-wrap .coupons .coupon-item:last-child{
		margin-right: 0px;
	}
	#shop-cart .goods-item{
		position: relative;
		width: 100%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#shop-cart .goods-item .goods-thumb{
		width: 70px;
		height: 70px;
		border-radius: 3px;
		overflow: hidden;
	}
	#shop-cart .goods-item .info{
		position: relative;
		height: 70px;
		flex: 1;
		padding-left: 15px;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#shop-cart .goods-item #goods-handle{
		position: absolute;
		right: 0;
		bottom: 0;
	}
	#shop-cart .total-wrap{
		position: fixed;
		bottom: 50px;
		left: 0;
		right: 0;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 15px;
		background-color: #fff;
	}
	#shop-cart .total-wrap .total{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-end;
		flex: 1
	}
	#shop-cart .total-wrap .total .info{
		padding-right: 10px;
		text-align: right;
	}
	#shop-cart .total-wrap .total .discount{
		font-size: 12px;
		margin-top: 5px;
	}
	#shop-cart .delete{
		position: fixed;
		z-index: 2001;
		top: 0;
		right: 15px;
		height: 46px;
		line-height: 46px;
		color: #fff;
	}
	#shop-cart .no-data img{
		width:100px;
		height:100px;
		position:absolute;
		left:50%;
		top:150px;
		transform:translate(-50%,0);
	}
	#shop-cart .no-data .target{
		position: absolute;
		left: 50%;
		top: 300px;
		transform: translate(-50%,0);
		font-size: 14px;
		background-color: #FF2D4B;
		color: #fff;
		padding: 8px 15px;
		border-radius: 15px;
	}
</style>


